import * as React from 'react';
import { useState, useEffect } from 'react';
import './ContactUsForm.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Alert from 'react-bootstrap/Alert';
import { faFacebook, faPinterest, faInstagram, faYoutube } from "@fortawesome/free-brands-svg-icons";


const ContactUsForm = () => {
  const [contact, setContact] = useState({
    Name: '',
    TownCity: '',
    Email: '',
    Message: '',
    HoneyPot: ''
  });

  const [response, setResponse] = useState({
    type: '',
    message: ''
  });

  const [validated, setValidated] = useState(false);

  function scrollToFirstValidationError() : void {

    var invalidElements = document.querySelectorAll("input.form-control:invalid, input.form-check-input:invalid");
    if(invalidElements.length > 0)
    {
      invalidElements[0].scrollIntoView({ behavior: "smooth", block: "center" });
    }
  }




  const handleChange = (e: any) =>
    setContact({ ...contact, [e.target.name]: e.target.value });


  const handleSubmit = async (e: any) => {
    e.preventDefault();
    if (e.currentTarget.checkValidity() === true) {
      try {
        const button = document.getElementById('submit-button') as HTMLButtonElement;
        button.disabled = true;
        button.innerHTML = 'Please wait...';
        const res = await fetch('https://api.finnsfureverfelinerescue.org/api/contact', {
          method: 'POST',
          body: JSON.stringify(contact),
          headers: { 'Content-Type': 'application/json' }
        });

        if (res.status === 200) {
          setResponse({
            type: 'success',
            message: 'Your form has been submitted successfully. You should receive a confirmation email shortly. If you do not receive it within a few minutes please check your "spam" or "junk" folder. Thank you for contacting us!'
          });
        } else {
          setResponse({
            type: 'error',
            message: 'There was an error submitting your form. Please try again later or contact us a different way.'
          });
        }
      } catch (e) {
        console.log('An error occurred', e);
        setResponse({
          type: 'error',
          message: 'There was an error submitting your form. Please try again later or contact us a different way.'
        });
      }
    }
    else
    {
      scrollToFirstValidationError();
    }
    setValidated(true);
  };

  return (
    <Container className="contact-us-container">
      <Row>
        <Col sm={12}>
          <h2>We'd love to hear from you!</h2>
        </Col>
      </Row>
      <Row>
        <Col sm={7}>
          <Row className={response.type === 'success' ? '' : 'hidden'}>
            <Alert variant="success" className="alert-message">
              {response.message}
            </Alert>
          </Row>
          <Row className={response.type === 'error' ? '' : 'hidden'}>
            <Alert variant="danger" className="alert-message">
              {response.message}
            </Alert>
          </Row>
        </Col>
        <Col sm={7} className={response.message !== '' ? 'hidden' : ''}>
          <Form method="post" onSubmit={handleSubmit}>
            <Container>
              <Row>
                <Col sm={12}>
                  <FloatingLabel controlId="Name" label="Name">
                    <Form.Control type="text" name="Name" placeholder="Name" onChange={handleChange} required />
                    <Form.Control.Feedback type="invalid">This field is required.</Form.Control.Feedback>
                  </FloatingLabel>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <FloatingLabel controlId="TownCity" label="Town/City">
                    <Form.Control type="text" name="TownCity" placeholder="TownCity" onChange={handleChange} required />
                    <Form.Control.Feedback type="invalid">This field is required.</Form.Control.Feedback>
                  </FloatingLabel>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <FloatingLabel controlId="Email" label="Email">
                    <Form.Control type="email" name="Email" placeholder="name@example.com" onChange={handleChange} required />
                    <Form.Control.Feedback type="invalid">This field is required and needs to be a valid email address.</Form.Control.Feedback>
                  </FloatingLabel>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <FloatingLabel controlId="floatingTextarea2" label="Message">
                    <Form.Control
                      as="textarea" name="Message"
                      placeholder="Type your message here"
                      style={{ height: '250px' }}
                      onChange={handleChange}
                      required
                    />
                  </FloatingLabel>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Button id="submit-button" variant="secondary" type="submit">
                    Submit
                  </Button>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Control type="text" name="HoneyPot" onChange={handleChange} style={{ display: "none" }} />
                </Col>
              </Row>
            </Container>
          </Form>
        </Col>
        <Col sm={5}>
          <Row>
            <Col sm={12}>
              <h4>Other ways to get in touch.</h4>
            </Col>
          </Row>
          <Row>
            <Col sm={12}>
              <Row>
                <Col sm={12}>
                  <strong>Email: </strong><a className="email-link" href="mailto:finnsfureverfelinerescue@yahoo.com">finnsfureverfelinerescue@yahoo.com</a>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                <div className="social-icon">
                  <a href="https://www.facebook.com/profile.php?id=100088392663475" target="_blank" className="facebook-link"><FontAwesomeIcon icon={faFacebook} size="2xl" />
                    <svg width="0" height="0">
                      <linearGradient x1="50%" x2="50%" y1="97.0782153%" y2="0%" id="facebook-logo-fill">
                        <stop offset="0%" stopColor="#0062E0"></stop>
                        <stop offset="100%" stopColor="#19AFFF"></stop>
                      </linearGradient>
                    </svg>
                  </a>
                </div>
                <div className="social-icon">
                  <a href="https://www.pinterest.com/finnsfureverfelinerescue/" target="_blank" className="pinterest-link"><FontAwesomeIcon icon={faPinterest} size="2xl" /></a>
                </div>
                <div className="social-icon">
                  <a href="https://www.instagram.com/finnsfureverfelinerescue" target="_blank" className="instagram-link"><FontAwesomeIcon icon={faInstagram} size="2xl" />
                    <svg width="0" height="0">
                      <radialGradient id="instagram-logo-fill" r="150%" cx="30%" cy="107%">
                        <stop stopColor="#fdf497" offset="0" />
                        <stop stopColor="#fdf497" offset="0.05" />
                        <stop stopColor="#fd5949" offset="0.45" />
                        <stop stopColor="#d6249f" offset="0.6" />
                        <stop stopColor="#285AEB" offset="0.9" />
                      </radialGradient>
                    </svg>
                  </a>
                </div>
                <div className="social-icon">
                  <a href="https://www.youtube.com/@FinnsFureverFelineRescue" target="_blank" className="youtube-link"><FontAwesomeIcon icon={faYoutube} size="2xl" /></a>  
                </div>


                  <Row>
                    <Col sm={3} className="social-icon">

                    </Col>
                    <Col sm={3} className="social-icon">
                      
                    </Col>
                    <Col sm={3} className="social-icon">

                    </Col>
                    <Col sm={3} className="social-icon">
                      
                      </Col>
                  </Row>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
}

export default ContactUsForm;